import React,{PureComponent} from 'react';
import { SearchBar, NavBar,Icon} from 'antd-mobile';

import '../assets/css/soso.css'
import {withRouter} from 'react-router-dom';
class SoSo extends PureComponent{
    state = {
        value: '美食',
    };
    componentDidMount() {
        this.autoFocusInst.focus();
    }
    onChange= (value) => {
        this.setState({ value });
    };
    clear = () => {
        this.setState({ value: '' });
    };
    handleClick = () => {
        this.manualFocusInst.focus();
    }
  render(){
    return (
        <>
            <NavBar
                mode="light"
                icon={<Icon type="left" />}
                onLeftClick={() => history.go(-1)}
            >{this.context.appData}</NavBar>
        <SearchBar placeholder="自动获取光标" ref={ref => this.autoFocusInst = ref} />
         </>
     )
    }
}

export default withRouter(SoSo)